@import "src/styles/vendor/import/_smart-grid.scss";

.footer{

    
    border-top: 1px solid grey;
    margin-top: 70px;

    &-container{

        @include container();

        &-row{
            @include row-flex();
            justify-content: center;

            &-text{

                text-align: center;
                font-size: 13px;
                font-weight: 400;
                font-stretch: normal;
                font-style: normal;
                letter-spacing: normal;
                text-align: left;
                color: #b5b5b5;
                margin-top: 25px;
                margin-bottom: 25px;
            }
        }
    }
}