@import "src/styles/vendor/import/_smart-grid.scss";

header{
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.advblock{
    background-color:#dae0e5;
    width: 100%;

    &-container{
        
        @include container();
        min-height: 30px;
        padding-top: 6px;

        &-row{
            @include row-flex();
            justify-content: center;
            flex-wrap: wrap;

            &-text{
                @include col();
                @include size(10);
                @include shift-left(1);
        
                display: inline-block;
                align-content: center;
                font-size: 10px;
                color: grey;
                text-align: center;
                text-transform: uppercase;
            }
        
            &-close{
                @include col();
                @include size(1);
        
                align-self: center;
                height: 10px;
                width: 9px;
                cursor: pointer;
            }
        }
    }    
}

.header{

    @include container();
    margin-top: 30px;

    &-row{
        @include row-flex();
        justify-content: space-between;

        &-title{
            align-self: center;
            @include col();

            text-decoration: none;
            color: black;
            
            font-size: 30px;
            font-weight: 800;
            text-align: center;
            line-height: 70px;

        }

        &-cart{
            border: 2px solid #ded3aa;
            width: 125px;
            height: 40px;
            padding-top: 25px;
            text-align: center;

            font-size: 14px;
            cursor: pointer;

            &:hover{
                background-color: #ded3aa;
            }
        }

        &-lang{
            padding-top: 25px;
        }
    }
}

.nav{

    margin-top: 30px;
    padding-top: 20px;
    padding-bottom: 20px;
    border-top: 1px solid grey;
    border-bottom: 1px solid grey;

    &-container{
        @include container();

        &-row{
            @include row-flex();
            justify-content: space-between;

            &-text{
                @include col();

                text-decoration: none;
                color: black;

                img{
                    margin-left: 7px;
                    margin-bottom: 3px;
                }
            }
            &-burger{
                @include col();

                margin-right: auto;
            }

            &-functions{
                @include col();
                margin-left: auto;
            }

        }
    }
}

.main{

    &-container{
        @include container();
        margin-top: 30px;

        &-row{
            @include row-flex();

            &-imgbox{
                @include col();
                justify-content: space-between;

                &-bigpic{

                    &:first-child{
                        min-width: 660px;
                        min-height: 680px;
                        background-color: lightgray;
                    }
                }

                &-smallpic{

                    display: inline-block;
                    background-color: lightgray;
                    min-width: 150px;
                    min-height: 170px;
                    background-color: lightgray;
                    margin-top: 20px;
                    margin-left: 10px;
                    margin-right: 10px;
                }
            }

            &-textbox{
                @include col();
                margin-left: 20px;

                &-title{
                    margin-top: 35px;
                    font-weight: 400;
                    font-size: 40px;
                }

                &-back{
                    margin-top: 10px;

                    a{
                        text-decoration: none;
                        color:cornflowerblue;
                    }
                }

                &-grade{

                    margin-top: 20px;

                    a{
                        color: black;
                    }

                    img{
                        display: inline-block;
                        cursor: pointer;
                    }

                    p{
                        display: inline;
                    }
                }

                &-price{

                    margin-top: 30px;
                    color: cornflowerblue;
                    font-size: 34px;
                }

                &-info{

                    ul{
                        list-style-type: none;
                        font-weight: 300;
                        margin-top: 25px;

                        a{
                            color: cornflowerblue;
                            text-decoration: none;
                        }

                        li{
                            margin-top: 5px;
                        };
                    }
                }

                &-description{
                    margin-top: 25px;
                    font-weight: 300;
                    font-size: 14px;

                    color: rgb(196, 195, 195);
                }

                &-params{

                    ul{
                        margin-left: 15px;
                        color: rgb(196, 195, 195);
                        font-weight: 300;
                        font-size: 14px;
                        margin-top: 25px;


                        li{
                            margin-top: 5px;
                        };
                    }
                }

                &-selection{

                    font-weight: 700;
                    font-size: 20px;
                    text-transform: uppercase;

                    ul{
                        list-style-type: none;
                        display: flex;
                        flex-direction: row;

                        li{
                            display: list-item;
                            margin-right: 25px;

                            select{
                                width: 170px;
                                height: 35px;

                                border: 1px solid #e1e1e1;
                                font-size: 14px;
                                font-weight: 300;

                                color: rgb(196, 195, 195);
                            }

                            .qty{
                                width: 50px;
                            }
                        }

                        
                        
                    }

                    
                }

                &-clear{

                    margin-top: 40px;
                    color: black;
                    text-decoration: underline;
                    text-transform: uppercase;
                    font-weight: 500;
                    font-size: 14px;
                }

                &-buttons{
                    
                    display: flex;
                    justify-content: space-between;

                    &-addcart{
                        height: 50px;
                        width: 220px;
                        color: white;
                        background-color: black;
                        text-align: center;
                        font-size: 16px;
                        padding-top: 32px;
                        margin-top: 20px;
                        cursor: pointer;
                    }

                    &-addwish{
                        height: 50px;
                        width: 220px;
                        color: black;
                        border: 1px solid black;
                        text-align: center;
                        font-size: 16px;
                        padding-top: 32px;
                        margin-top: 20px;
                        cursor: pointer;
                    }

                }

                &-share{
                    display: flex;
                    justify-content: space-between;
                    margin-top: 20px;
                    border-top: 1px solid grey;
                    border-bottom: 1px solid grey;

                    padding-top: 10px;
                    padding-bottom: 10px;
                }
            }


        }
    }
}